body {
  letter-spacing: .04em;
  margin: 0;
  font-family: Poppins, sans-serif;
  font-style: normal;
  font-weight: 500;
  position: relative;
}

body.light-theme {
  color: #202025;
  background-color: #fff;
}

body.dark-theme {
  color: #fff;
  background-color: #202025;
}

a {
  color: currentColor;
  text-decoration: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

button {
  cursor: pointer;
}

.light {
  color: #202025;
  fill: #202025;
  background-color: #fcfcfc;
}

.dark {
  color: #fcfcfc;
  fill: #fcfcfc;
  background-color: #202025;
}

body.disable-scroll {
  overflow: hidden;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background: #bbb;
}

::-webkit-scrollbar-thumb {
  background: #fe7031;
}

p {
  margin: 0;
  padding: 0;
}

ul, ol, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

* {
  box-sizing: border-box;
}

