.burger {
  width: 30px;
  height: 26px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 10px;
  display: none;
  position: relative;
}

@media (max-width: 1023px) {
  .burger {
    display: block;
  }
}

.burger__line {
  width: 100%;
  height: 2px;
  background-color: #000;
  border-radius: 1px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.burger:before, .burger:after {
  width: 100%;
  height: 2px;
  content: "";
  background-color: #000;
  border-radius: 1px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), top .25s cubic-bezier(.4, 0, .2, 1), bottom .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  left: 0;
}

.burger:before {
  top: 0;
}

.burger:after {
  bottom: 0;
}

.burger-active .burger__line {
  opacity: 0;
}

.burger-active:before {
  top: 50%;
  transform: rotate(45deg);
}

.burger-active:after {
  top: 50%;
  bottom: auto;
  transform: rotate(-45deg);
}

