}

@media (min-width: 768px) and (max-width: 1023px) {
  .theme-switch {
    opacity: 0;
    transition: opacity .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1);
    position: absolute;
    top: 40%;
    left: 220px;
    transform: translateY(-100%);
  }
}

@media (min-width: 1024px) {
  .theme-switch {
    margin-right: 20px;
