.theme-switch__label {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.33;
}

.theme-switch__control {
  width: 40px;
  height: 16px;
  border: 1px solid #fd5103;
  border-radius: 20px;
  flex-shrink: 0;
  align-items: center;
  margin: 0 5px;
  padding: 2px 0;
  display: flex;
  position: relative;
  top: 0;
  left: 0;
}

.theme-switch__toggle {
  width: 0;
  height: 0;
  opacity: 0;
  pointer-events: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.theme-switch__track {
  width: 100%;
  height: 100%;
  color: rgba(0, 0, 0, 0);
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border-radius: inherit;
  z-index: 1;
  cursor: pointer;
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
}

.theme-switch__marker {
  width: 12px;
  height: 12px;
  z-index: 2;
  pointer-events: none;
  background-color: #fd5103;
  border-radius: 50%;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
  top: 0;
  left: 0;
  transform: translateX(2px);
  box-shadow: 0 1px 1px rgba(0, 0, 0, .25);
}

.theme-switch__toggle:checked + .theme-switch__track {
  background-color: #fd5103;
}

.theme-switch__toggle:checked ~ .theme-switch__marker {
  background-color: #fff;
  transform: translateX(24px);
}

.theme-switch__toggle:focus + .theme-switch__track, .theme-switch__toggle:active + .theme-switch__track {
  box-shadow: 0 0 0 2px #9acd32;
}

.theme-switch-active {
  color: #fd5103;
}

