.loader {
  --s: 40px;
  --b: 18px;
  width: 280px;
  aspect-ratio: 4;
  margin-left: auto;
  margin-right: auto;
  display: grid;
}

.loader:before, .loader:after {
  content: "";
  --_g: calc(-.45 * var(--s) ), rgba(0, 0, 0, 0) calc(99% - var(--b) ), #000 calc(101% - var(--b) ) 99%, rgba(0, 0, 0, 0) 101%;
  --_s: calc(4 * var(--s) ) var(--s) repeat-x;
  --_r: calc(1.096 * var(--s)  + var(--b) / 2);
  -webkit-mask: radial-gradient(var(--_r) at left 50% bottom var(--_g) ) calc(50% - 2 * var(--s) ) calc(50% - var(--s) / 2) / var(--_s), radial-gradient(var(--_r) at left 50% top var(--_g) ) 50% calc(50% + var(--s) / 2) / var(--_s);
  mask: radial-gradient(var(--_r) at left 50% bottom var(--_g) ) calc(50% - 2 * var(--s) ) calc(50% - var(--s) / 2) / var(--_s), radial-gradient(var(--_r) at left 50% top var(--_g) ) 50% calc(50% + var(--s) / 2) / var(--_s);
  background-color: rgba(0, 0, 0, 0);
  background-image: linear-gradient(#fd5103 50%, #8d1f00 0), linear-gradient(#fd5103 50%, #8d1f00 0);
  background-position: 0 0, 0 0;
  background-repeat: no-repeat, no-repeat;
  background-size: 50% 200%;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  grid-area: 1 / 1;
  animation: back 6s step-end infinite, load 1.5s linear infinite alternate;
}

.loader:after {
  animation-delay: -1.5s;
  transform: scaleY(-1);
}

@keyframes load {
  0% {
    -webkit-clip-path: inset(0 75% 0 0);
    clip-path: inset(0 75% 0 0);
  }

  to {
    -webkit-clip-path: inset(0 0 0 75%);
    clip-path: inset(0 0 0 75%);
  }
}

@keyframes back {
  0%, 100% {
    background-position: 0 0, 100% 0;
  }

  25% {
    background-position: 0 100%, 100% 0;
  }

  50% {
    background-position: 0 100%, 100% 100%;
  }

  75% {
